import { Component } from '@angular/core';

@Component({
  selector: 'app-item-details',
  templateUrl: './item-details.component.html',
  styleUrls: ['./item-details.component.css']
})

export class ItemDetailsComponent {


   rows: any[] = [
    {
      commodity: 'Data 1',
      length: 100.23
    },
    {
      commodity: 'Data 2',
      length: 0.875623
     
    },
    {
      commodity: 'Data 3',
      length: .010123
     
    },
    {
      commodity: 'Data 4',
      length: 1873.02301
    },
    {
      commodity: 'Data 5',
      length: -93
    }
  ];
  columns: any[] = [
    {
      display: 'Commodity', 
      field: 'commodity', 
      filter: 'text',
      dropdown: true
    },
    {
      display: 'Msrmt', 
      field: 'msrmt', 
      filter: 'decimal : 1.0-2',
      dropdown:true
    },
    {
      display: 'Details of the Content', //The text to display
      field: 'content', //The name of the key that's apart of the data array
      filter: 'text' //The type data type of the column (number, text, date, etc.)
    },
    {
      display: 'Length', //The text to display
      field: 'length', //The name of the key that's apart of the data array
      filter: 'decimal : 1.0-2'//The type data type of the column (number, text, date, etc.)
    },
    {
      display: 'Width', //The text to display
      field: 'width', //The name of the key that's apart of the data array
      filter: 'number' //The type data type of the column (number, text, date, etc.)
    },
    {
      display: 'Height', //The text to display
      field: 'height', //The name of the key that's apart of the data array
      filter: 'number' //The type data type of the column (number, text, date, etc.)
    },
    {
      display: 'CFT Vol', //The text to display
      field: 'cftvol', //The name of the key that's apart of the data array
      filter: 'decimal : 1.0-2' //The type data type of the column (number, text, date, etc.)
    },
    {
      display: 'CFT Wt', //The text to display
      field: 'cftwt', //The name of the key that's apart of the data array
      filter: 'decimal : 1.0-2' //The type data type of the column (number, text, date, etc.)
    },
    {
      display: 'Act Wt', //The text to display
      field: 'actwt', //The name of the key that's apart of the data array
      filter: 'decimal : 1.0-2' //The type data type of the column (number, text, date, etc.)
    },
    {
      display: 'Chrgd Wt', //The text to display
      field: 'chrdwt', //The name of the key that's apart of the data array
      filter: 'decimal : 1.0-2' //The type data type of the column (number, text, date, etc.)
    },
     {
      display: 'Artcles', //The text to display
      field: 'artcles', //The name of the key that's apart of the data array
      filter: 'text' //The type data type of the column (number, text, date, etc.)
    }
  ];
  sorting: any = {
    column: 'Length', //to match the field of one of the columns
    descending: false
  };

  addNew() {
        this.rows.push( {
      commodity: 'Data 6',
      length: 200
    })
    }
}



